<script>
  const APP = getApp();
  import {
  	getYear,
  	getMonth,
  	getWeekNum,
  	getQuarter
  } from '../../utils/utils.js'
  import {getIndexInfo} from '../../utils/http.js';
  export default {
    data() {
      return {
		info:{},
		imgUrl: APP.globalData.imgUrl,
		opts: {
		  	title: {
		  		name: "",
		  		fontSize: 12,
		  	},
			subtitle: {
		  		name: "月达成",
				color: "#3C61DF",
		  		fontSize: 12,
		  	},
		  	extra: {
		  		arcbar: {
		  			type: "circle",
		  			lineCap: "butt"
		  		}
		  	}
		  }, //进度条样式
		chartData:  {}, //进度条数据
		list:[{symbol:''}]
      }
    },
    onShow() {
		uni.setStorage({
			key: 'current',
			data: {
				toYear: getYear(),
				toMonth: getMonth(), 
				toWeek: getWeekNum(), 
				toSeason: getQuarter()
			}
		})
    },
	 computed:{
		 statusBarClientRectTop(){
		 	var that = this;
		 				// #ifdef H5
		 				return '10px'
		 				// #endif
		 				
		 				// #ifdef APP-PLUS
		 				return (uni.getSystemInfoSync().statusBarHeight + 2) *2 + 'rpx'
		 				// #endif
		 						
		 				// #ifdef MP-WEIXIN
		 				  return `${
		 					uni.getMenuButtonBoundingClientRect().top +2
		 				  }px`;
		 				  // #endif 
		 				    
		 },
	 },
	created() { 
		this.getRegion()
	},
    methods: {
		swiperChange(event) {
			this.currentIndex = event.detail.current
		},
		currency(v){
			let val = parseFloat(v || 0)
			val = isNaN(val) ? undefined : val
			return Number(val.toFixed(0)).toLocaleString()
		},
		handleSwiperClick(index){
			this.currentIndex = index
		},
		getRegion(value){
			this.currentRegion=value;
			this.regionShow=false;
			this.getInfo();
		},
	
	  getInfo(){
		  let that=this;
		   getIndexInfo().then(res => {
			   this.info= res.data
			   let list = {
			   	series: [{
			   		name: "达成率",
			   		color: "#3C61DF",
			   		data: res.data.mon_achievement/100
			   	}]
			   };

			   this.opts.title.name = res.data.mon_achievement +'%'
			   this.chartData = JSON.parse(JSON.stringify(list))
		   })
	  },
	  back(index){
		if(index==0){
			uni.navigateTo({
				url:"/pages/SalesAnalysisNew/SalesAnalysisNew"
			})
		}
	  }
    }
  }
</script>

